@import url(https://fonts.googleapis.com/css2?family=Lato:wght@300;400&family=Roboto:wght@300;400;500;700&display=swap);

html, body, p {
  margin: 0;
}

a {
  text-decoration: none;
}
.background {
    width: 100%;
    height: 100vh;
    -webkit-align-items: center;
            align-items: center;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
.main {
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    max-width: 1200px;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
    margin-bottom: auto;
}
.header {
    min-height: 60px;
    overflow: hidden;
    word-wrap: normal;
    width: 100%;
    background: #c22e1d;
    box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.2);
    position: -webkit-sticky;
    position: sticky; 
    top: 0;
}

.header-text {
    font-size: 28px;
    padding: 11px;
    font-family: Roboto;
    padding-left: 15px;
    font-weight: 300;
}

.header-text a {
    color: white;
}
.footer {
    min-height: 80px;
}

.footer-text {
    font-size: 18px;
    font-family: Lato;
    color: black;
    text-align: center;
    padding-top: 25px;
    font-weight: 400;
    margin-top: auto;
}
.card {
    border-radius: 7px;
    overflow: hidden;
    width: calc(100% - 80px);
    max-width: 400px;
    margin: 15px;
    margin-bottom: 0px;
    margin-top: 25px;
    padding: 25px;
    box-shadow: 0 5px 10px 0px rgba(0, 0, 0, 0.2);
    -webkit-flex-grow:1 1;
            flex-grow:1 1;
}

.card-spacer {
    min-width: 100%;
    min-height: 25px;
}

.card-title {
    font-family: Roboto;
    color: black;
    font-size: 22px;
    font-weight: 400;
}

.card-text p {
    font-family: Lato;
    font-size: 16px;
}

.card-link {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: end;
            justify-content: end;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.card-link a {
    color: #c22e1d;
    font-family: Lato;
    text-align: right;
    width: 100%
}
